import { useState } from 'react';
import { ExpandSVG } from '../../../assets/expand.js';
import { CollapseSVG } from '../../../assets/collapse.js';

const fields = {
    isPainMed: false,
    isAirwayMed: false,
    isFluid: false,
    isSedation: false,
    isContraindicated: false,
};

const routes = {
    IV: false,
    IO: false,
    IN: false,
    PO: false,
    IM: false,
    Inhalation: false,
};

const Medication = ({ med, index, medState }) => {
    const [meds, setMeds] = medState;
    const [self, setSelf] = useState(med);
    const [expanded, setExpanded] = useState(false);
    const [mightExpand, setMightExpand] = useState(false);

    // Backwards compatability
    if (!med.adult) med.adult = { ...fields, routes: {...routes} };
    if (!med.pediatric) med.pediatric = { ...fields, routes: {...routes} };

    function update(e) {
        let updated = {...self};
        const item = e.target;
        const isAdult = item.dataset.isAdult === "true";

        const value = item.type === "checkbox" ? item.checked : item.value;

        if (item.name.includes("route-")) {
            updated[isAdult ? "adult" : "pediatric"].routes
                [item.value] = value;
        }
        else {
            if (item.dataset.isAdult) {
                updated[isAdult ? "adult" : "pediatric"]
                    [item.name] = value;
            }
            else {
                updated[item.name] = value;
                setSelf(updated)
            }
        }

        meds[index] = updated;
    }

    function deleteSelf() {
        meds.splice(index, 1);
        setMeds(meds.slice());
    }

    if (self.routes) {
        delete self.routes;
        delete self.isAirwayMed;
        delete self.isFluid;
        delete self.isPainMed;

        setSelf({...self});
    }

    return (
        <div className="medication">
            <div className="main-area">
                <button type="button" className="delete-bttn" onClick={deleteSelf}>-</button>
                <h3>{self.label ?? "New Medication"}</h3>

                <input
                    type="text"
                    className="id"
                    id={`${self.id}-id`}
                    placeholder="ID"
                    name="id"
                    onChange={update}
                    defaultValue={med.id}
                    />

                <input
                    type="text"
                    className="label"
                    id={`${self.id}-label`}
                    placeholder="Label"
                    name="label"
                    onChange={update}
                    defaultValue={med.label} />

                <button
                    type="button"
                    className={`expand-bttn ${expanded ? "expanded" : ""}`}
                    onClick={() => setExpanded(!expanded)}
                    onMouseEnter={() => setMightExpand(true)}
                    onMouseLeave={() => setMightExpand(false)}
                >
                    {(!expanded && mightExpand) || (expanded && !mightExpand) ? <ExpandSVG /> : <CollapseSVG />}
                </button>
            </div>

            <div className={`expanded-area ${expanded ? "expanded" : "collapsed"}`}>
                <div className="headings">
                    <p></p>
                    <p>Initial Dosage</p>
                    <p>Dosage</p>
                    <p>Max Total Administered</p>
                    <p>Interval (minutes)</p>
                    <p>Pain?</p>
                    <p>Airway?</p>
                    <p>Fluid?</p>
                    <p>Sedation?</p>
                    <p>Contraindicated?</p>
                </div>
                <div className="adult-data">
                    <div className="basic-info">
                        <p>Adult:</p>
                        <MedGroup isAdult={true} group={med.adult} />
                    </div>

                    <div className="administration-routes">
                        <p>Routes:</p>
                        <Route route="IV/IO" isadult={true} />
                        <Route route="IM" isadult={true} />
                        <Route route="IN" isadult={true} />
                        <Route route="PO" isadult={true} />
                        <Route route="Inhalation" isadult={true} />
                    </div>
                </div>

                <div className="pediatric-data">
                    <div className="basic-info">
                        <p>Pediatric:</p>
                        <MedGroup isAdult={false} group={med.pediatric} />
                    </div>

                    <div className="administration-routes">
                        <p>Routes:</p>
                        <Route route="IV/IO" isadult={false} />
                        <Route route="IM" isadult={false} />
                        <Route route="IN" isadult={false} />
                        <Route route="PO" isadult={false} />
                        <Route route="Inhalation" isadult={false} />
                    </div>
                </div>
            </div>
        </div>
    );

    function Route({ route, isadult }) {
        return (
            <label>
                <input
                    type="checkbox"
                    id={`${self.id}-route-${route}-${isadult ? "adult" : "pediatric"}`}
                    name={`route-${med.id}`}
                    value={route}
                    onChange={update}
                    data-is-adult={isadult}
                    defaultChecked={med[isadult ? "adult" : "pediatric"].routes?.[route]}
                />
                {route}
            </label>
        );
    }

    function MedGroup({ group, isAdult }) {
        return (
            <>
            <input
                type="text"
                name="initialDosage"
                id={`${self.id}-initialDosage-${isAdult ? "adult" : "pediatric"}`}
                placeholder="Initial Dosage"
                data-is-adult={isAdult}
                onChange={update}
                defaultValue={group.initialDosage} />

            <input
                type="text"
                name="dosage"
                id={`${self.id}-dosage-${isAdult ? "adult" : "pediatric"}`}
                placeholder="Dosage"
                data-is-adult={isAdult}
                onChange={update}
                defaultValue={group.dosage} />

            <input
                type="text"
                name="maxAdministered"
                id={`${self.id}-maxAdministered-${isAdult ? "adult" : "pediatric"}`}
                placeholder="Max Total Administered"
                data-is-adult={isAdult}
                onChange={update}
                defaultValue={group.maxAdministered} />

            <input
                type="number"
                name="interval"
                id={`${self.id}-interval-${isAdult ? "adult" : "pediatric"}`}
                placeholder="Interval (minutes)"
                data-is-adult={isAdult}
                onChange={update}
                defaultValue={group.interval} />

            <input
                type="checkbox"
                id={`${self.id}-isPainMed-${isAdult ? "adult" : "pediatric"}`}
                name="isPainMed"
                defaultChecked={group.isPainMed}
                data-is-adult={isAdult}
                onChange={update} />

            <input
                type="checkbox"
                id={`${self.id}-isAirwayMed-${isAdult ? "adult" : "pediatric"}`}
                name="isAirwayMed"
                data-is-adult={isAdult}
                defaultChecked={group.isAirwayMed}
                onChange={update} />

            <input
                type="checkbox"
                id={`${self.id}-isFluid-${isAdult ? "adult" : "pediatric"}`}
                name="isFluid"
                data-is-adult={isAdult}
                defaultChecked={group.isFluid}
                onChange={update} />

            <input
                type="checkbox"
                id={`${self.id}-isSedation-${isAdult ? "adult" : "pediatric"}`}
                name="isSedation"
                data-is-adult={isAdult}
                defaultChecked={group.isSedation}
                onChange={update} />

            <input
                type="checkbox"
                id={`${self.id}-isContraindicated-${isAdult ? "adult" : "pediatric"}`}
                name="isContraindicated"
                data-is-adult={isAdult}
                defaultChecked={group.isContraindicated}
                onChange={update} />
            </>
        );
    }
}

export default Medication